:root{
  --bg:#141824;
  --bg-2:#1a1f2e;
  --card:#1f2638;
  --text:#e7e9ee;
  --muted:#9aa3b2;
  --accent:#ffb028;
  --accent-2:#ff6b3d;
  --ok:#2ecc71;
  --err:#ff5a5a;
  --ring: rgba(255,176,40,.35);
}

*{box-sizing:border-box}

html,body,#root,.app{height:100%}
body{
  margin:0;
  font-family: ui-sans-serif,system-ui,-apple-system,Segoe UI,Roboto,Arial,Apple Color Emoji,Segoe UI Emoji;
  color: var(--text);
  background: radial-gradient(1200px 800px at 20% -10%, #1e2340, #141824 60%);
}

.app-header{
  padding: 18px 24px;
  border-bottom: 1px solid rgba(255,255,255,.06);
  background: linear-gradient(180deg, rgba(255,255,255,.02), transparent);
  position: sticky; top:0; z-index: 2;
}
.brand{display:flex; align-items:center; gap:.6rem; font-weight:800; letter-spacing:.2px}
.brand-logo{font-size:1.4rem}
.brand-name{font-size:1.3rem}

.app-main{
  min-height: calc(100% - 120px);
  display:flex; align-items:center; justify-content:center;
  padding: clamp(16px, 4vmin, 48px);
}

.app-footer{
  text-align:center;
  color: var(--muted);
  font-size:.95rem;
  padding: 16px 24px 28px;
}

.game-shell{
  width: min(1100px, 96vw);
  margin: 0 auto;
  display: grid;
  gap: 18px;
  justify-items: center;
}

.score-bar{
  width: 100%;
  max-width: 900px;
  display:flex; align-items:center; justify-content:space-between;
  padding: 10px 14px;
  background: rgba(255,255,255,.04);
  border: 1px solid rgba(255,255,255,.06);
  border-radius: 12px;
  gap: 16px;
}

.score-item{
  display:flex; align-items:center; gap:8px;
}
.score-item span{
  color: var(--muted);
}

.card{
  width: 100%;
  max-width: 900px;
  padding: clamp(16px, 3.2vmin, 32px);
  background: var(--card);
  border: 1px solid rgba(255,255,255,.07);
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0,0,0,.35);
  display: grid;
  gap: 18px;
  justify-items: center;
}

.progress{
  display:flex; gap:10px; align-items:center; justify-content:center;
}
.dot{
  width: 14px; height: 14px; border-radius: 999px;
  background: rgba(255,255,255,.14);
  border: 1px solid rgba(255,255,255,.16);
}
.dot.active{
  background: var(--accent);
  box-shadow: 0 0 0 6px var(--ring);
  border-color: rgba(0,0,0,.2);
}

.sprite-wrap {
  width: 520px;
  height: 520px;
  display: flex;
  flex-direction: column;   /* colonne verticale */
  align-items: center;      /* centre horizontalement */
  background: #0f1424;
  border-radius: 12px;
  border: 2px solid rgba(255,255,255,.08);
  box-sizing: border-box;
  overflow: hidden;
  padding: 10px;
}

.sprite-img {
  height: 100%;     /* prend toute la hauteur possible */
  width: auto;      /* ajuste largeur pour garder ratio */
  image-rendering: pixelated;
  image-rendering: crisp-edges;
  display: block;
  margin-bottom: 8px;  /* espace sous lâ€™image */
}

.caption {
  color: var(--muted);
  font-size: 0.95rem;
  user-select: none;
}

.controls{
  width: 100%;
  display:grid; gap: 12px;
}
.input-row{
  display:grid; grid-template-columns: 1fr auto; gap: 10px;
}
.secondary-row{
  display:flex; align-items:center; gap: 10px; justify-content: space-between;
  color: var(--muted);
}

.guess-input{
  width:100%;
  padding: 14px 14px;
  font-size: 1.05rem;
  color: var(--text);
  background: var(--bg-2);
  border: 1px solid rgba(255,255,255,.1);
  border-radius: 10px;
  outline: none;
}
.guess-input:focus{
  border-color: var(--accent);
  box-shadow: 0 0 0 4px var(--ring);
}

.btn{
  padding: 12px 16px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-weight: 700;
  letter-spacing:.2px;
}
.btn.primary{
  background: linear-gradient(180deg, var(--accent), var(--accent-2));
  color: #1a1308;
}
.btn.ghost{
  background: transparent;
  border: 1px solid rgba(255,255,255,.18);
  color: var(--text);
}
.btn.next{
  background: #2ee6a4; color: #071a12;
}
.btn.ghost.danger{
  border-color: rgba(255,90,90,.35);
  color: #ffd6d6;
}

.btn:disabled{
  opacity:.55; cursor:not-allowed;
}

.hint{color: var(--muted)}
.message{
  width:100%;
  text-align:center;
  padding: 10px 12px;
  border-radius: 10px;
  background: rgba(255,255,255,.04);
}
.message:has(> .ok), .ok{ color: var(--ok) }
.err{ color: var(--err) }

.post-round{
  display:grid; gap: 10px; justify-items:center;
}

.poster-wrap{
  display:grid; gap: 12px; justify-items:center;
}
.poster-img{
  width: clamp(280px, 40vmin, 560px);
  max-height: 70vh;
  object-fit: cover;
  border-radius: 14px;
  border: 1px solid rgba(255,255,255,.12);
  box-shadow: 0 20px 60px rgba(0,0,0,.35);
}

.help{
  color: var(--muted);
  font-size: .9rem;
}

/* Animations */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(8px) scale(.98); }
  to   { opacity: 1; transform: translateY(0) scale(1); }
}
.fade-in{ animation: fadeIn .35s ease both }

.menu-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: radial-gradient(circle at center, #1e1e2f, #141824 80%);
  color: var(--text);
  text-align: center;
  padding: 0 24px;
}

.menu-title {
  font-size: clamp(3rem, 10vw, 6rem);
  margin-bottom: 40px;
  font-weight: 900;
  letter-spacing: 3px;
  text-shadow: 0 0 10px var(--accent);
}

.menu-buttons {
  display: flex;
  gap: 24px;
  flex-wrap: wrap;
  justify-content: center;
}

.menu-buttons .btn {
  padding: 24px 40px;
  font-size: 1.5rem;
  font-weight: 800;
  border-radius: 14px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.menu-buttons .btn:hover {
  background: var(--accent-2);
  color: #111;
}

.guesspokemon-container {
  max-width: 600px;
  margin: 40px auto;
  padding: 24px;
  background: var(--card);
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0,0,0,.35);
  color: var(--text);
  text-align: center;
}

.guesspokemon-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.pokemon-display {
  width: 300px;
  height: 300px;
  margin: 0 auto 24px;
  position: relative;
}

.pokemon-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition: filter 0.6s ease, opacity 0.6s ease;
  image-rendering: pixelated;
  user-select: none;
  pointer-events: none;
}

.silhouette {
  filter: brightness(0) saturate(100%) opacity(0.9);
  position: absolute;
  top: 0;
  left: 0;
  opacity: 1;
  z-index: 1;
}

.revealed {
  opacity: 1;
  z-index: 2;
}

.fade-in {
  opacity: 0;
  animation: fadeIn 0.6s forwards;
}

.guess-input {
  width: 100%;
  padding: 14px 16px;
  font-size: 1.1rem;
  border-radius: 10px;
  border: none;
  outline: none;
  margin-bottom: 16px;
  background: var(--bg-2);
  color: var(--text);
}

.guess-input:disabled {
  background: #222;
  color: #555;
}

.message {
  min-height: 24px;
  margin-bottom: 20px;
  font-weight: 600;
}

.btn {
  padding: 12px 20px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-weight: 700;
  background: var(--accent);
  color: #1a1308;
  transition: background 0.3s ease;
}

.btn.primary:hover {
  background: var(--accent-2);
}

.btn.ghost {
  background: transparent;
  border: 1px solid rgba(255,255,255,0.18);
  color: var(--text);
  font-weight: 600;
  cursor: pointer;
}

.btn.ghost:hover {
  background: rgba(255,255,255,0.05);
}

.score-bar {
  margin-top: 28px;
  font-size: 1.2rem;
  font-weight: 700;
}
